// src/components/MovieCard.js

import React, { useState } from 'react';
import CWMovieModal from './CWMovieModal';
import defaultImage from '../assets/images/currentWorks/default.png';

const MovieCard = ({ movie }) => {
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const { title, image } = movie;

  // Dynamically import the image
  let imageSrc;
  try {
    imageSrc = require(`../assets/images/currentWorks/${image}`);
  } catch (err) {
    imageSrc = defaultImage;
  }

  return (
    <>
      <div
        className="relative cursor-pointer group"
        onClick={() => setModalIsOpen(true)}
      >
        <img
          src={imageSrc}
          alt={title}
          className="w-full h-64 object-cover rounded-lg shadow-md"
        />
        <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-60 transition-opacity duration-300 flex items-center justify-center rounded-lg">
          <h3 className="font-subheader text-white text-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-300">
            {title}
          </h3>
        </div>
      </div>

      <CWMovieModal
        isOpen={modalIsOpen}
        onRequestClose={() => setModalIsOpen(false)}
        movie={movie}
      />
    </>
  );
};

export default MovieCard;
